<template>
<v-navigation-drawer
    :rail="rail"
    permanent
    v-if="!screenSize"
    >
    <v-list nav density="compact">
        <v-list-item
        v-for="item in items"
        :key="item.title"
        :prepend-icon="item.icon"  :value="item.title" :class="rail ? 'my-4 rounded-pill list' : 'rounded-pill list mx-2 pa-4 black--text'"  active-class="activeItem font-weight-bold black--text"
        :to=" item.path "
        :disabled="item.disabled"
        >
        
            <p>{{ item.title }}</p>
        
        
        </v-list-item>
    </v-list>
    <v-menu v-if="rail"  >
        <template v-slot:activator="{ props }">
            <ProfilePic  class="ml-2 pointer" :name="inputName" v-bind="props"></ProfilePic>
        </template>
        <v-list flat elevation="0" class="rounded-xl pointer" >
            <v-list-item elevation="0" v-on="on" @click.stop>
                <v-list-item-title>
                <Profile></Profile>
                </v-list-item-title>
            </v-list-item>
        </v-list>
    </v-menu>
    
    <Profile v-else :class="rail ? 'pl-2 rounded-pill list' : 'ma-4 pl-4 rounded-pill list'" ></Profile>
        <!--<v-list-item prepend-icon="mdi-forum" title="About" value="about"></v-list-item>-->
</v-navigation-drawer >
    <v-bottom-navigation v-if="screenSize" elevation="0">
        <v-btn value="recent" v-for="item in itemsBottom"
        :key="item.title"
        :to=" item.path "
        :disabled="item.disabled"
        class="rounded-pill font-weight-light"
        >
            <v-icon>{{item.icon}}</v-icon>
        </v-btn>
    </v-bottom-navigation>
</template>

<script setup>
import { ref, onMounted, watch,  } from 'vue'
import Profile from '../components/Profile.vue'
import ProfilePic from '../components/ProfilePicture.vue'

const inputName = ref('')
//const handle = ref('')
const rail = ref(false)
const screenSize = ref('')
const profilePath = ""

const items = [
    { title: "Home", icon: "mdi-home", path: "/", disabled: false },
    { title: "Explore", icon: "mdi-pound", path: "/home", disabled: false},
    { title: "Notifications", icon: "mdi-bell-outline", path: "/home", disabled: true },
    { title: "Bookmarks", icon: "mdi-bookmark-outline", path: "/home", disabled: true },
    
]

const itemsBottom = [
    { title: "Home", icon: "mdi-home", path: "/", disabled: false },
    { title: "sEARCH", icon: "mdi-magnify", path: "/home", disabled: false },
    { title: "Notifications", icon: "mdi-bell-outline", path: "/home", disabled: true },
    { title: "Profile", icon: "mdi-account-outline", path: "/home", disabled: true },
]
//const emit = defineEmits(['updateName'])

watch(inputName, (newVal) => {
    localStorage.setItem('name', newVal)
    //emit('updateName', inputName)
})

const size = () => {
    rail.value = window.matchMedia('(max-width: 1300px)').matches ? true : false 
    screenSize.value = window.matchMedia('(max-width: 500px)').matches ? true : false 
}


onMounted(() => {
    inputName.value = localStorage.getItem('name') || ''
    size()
    window.addEventListener('resize', size)
    items.push({ title: "Profile", icon: "mdi-account-outline", path: "/profile/"+inputName.value, disabled: false })
    itemsBottom.push({ title: "Profile", icon: "mdi-account-outline", path: "/profile/"+inputName.value, disabled: false })
    //console.log("Your screen resolution is: " + window.screen.width * window.devicePixelRatio + "x" + window.screen.height * window.devicePixelRatio)
})


</script>

<style >
.activeItem {
  background-color: white;
  color: black !important;
  font-weight: bold;
}
</style>