<template>
  <v-app>
    <v-main>
      <v-row>
      <side-bar/>
        <v-col lg="3" md="1" sm="1" v-if="!screenSize"></v-col>
        <v-col  lg="6" md="7" sm="9" xs="12">
          
            <RouterView></RouterView>
          
        </v-col>
        <v-col  lg="3" md="3" sm="1" xs="0" v-if="!screenSize">
          
        </v-col>
      </v-row>
    </v-main>
  </v-app>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { RouterView } from 'vue-router';
import SideBar from './components/SideBar.vue'

const screenSize = ref('')

const size = () => {
    screenSize.value = window.matchMedia('(max-width: 600px)').matches ? true : false 
}

onMounted(() => {
    size()
    window.addEventListener('resize', size)
    
})
</script>
