<template>
<v-sheet>
        <v-row>
        
            <v-col cols="3" class="">
                <ProfilePic :name="inputName">
                </ProfilePic>
            </v-col>

            <v-col cols="9">
                <v-row>
                <v-col>
                    <input
                    placeholder="name"
                    v-model="inputName"
                    class="noBorder font-weight-bold"
                    />
                </v-col>
                    
                </v-row>
                <v-row no-gutters justify="start" align="center">
                    <v-col cols="1" >
                    <p class="text-medium-emphasis">@ </p>
                    </v-col>
                    <v-col cols="11">
                        <input
                        placeholder="handle"
                        class="noBorder text-medium-emphasis"
                        />
                    </v-col>
                    
                </v-row>
                
                
            </v-col>

        </v-row>
        <v-row></v-row>
</v-sheet>

</template>

<script setup>
import { ref, onMounted, watch } from 'vue'
import ProfilePic from '../components/ProfilePicture.vue'

const inputName = ref('')

watch(inputName, (newVal) => {
    localStorage.setItem('name', newVal)
    //emit('updateName', inputName)
})

onMounted(() => {
    inputName.value = localStorage.getItem('name') || ''
    //size()
    //window.addEventListener('resize', size)
    //console.log("Your screen resolution is: " + window.screen.width * window.devicePixelRatio + "x" + window.screen.height * window.devicePixelRatio)
})

</script>